import './scss/app.scss';
import AOS from './aos.js';
import Parallax from '../node_modules/parallax-js/src/parallax.js';
import Typewriter from '../node_modules/typewriter-effect/dist/core';

// Parallaxe
let scene = document.getElementById('scene');
let parallaxInstance = new Parallax(scene, {
    relativeInput: true
});
parallaxInstance.friction(0.05, 0.05);
parallaxInstance.limit(39, 119);

// Typewriter
new Typewriter('#typewriter', {
    strings: ["randonner", "dcouvrir", "partager"],
    autoStart: true,
    loop: true,
    delay: 100,
    deleteSpeed: 35,
    pauseFor: 6000,
});

window.addEventListener("scroll", (e) => {
    if (window.scrollY >= 10) {
        document.querySelector('#globalNav').classList.add('bg-white');
    } else {
        document.querySelector('#globalNav').classList.remove('bg-white');
    }
})

// Faune & Flore
let fauneRow = document.querySelector('#faune-row');
let floreRow = document.querySelector('#flore-row');



function changeFauneFlore() {
    setTimeout(function() {
        var check = false
        console.log("\n--- Iteration begin ---")
        if (check == false && fauneRow.classList.contains('hidden-az') && !floreRow.classList.contains('hidden-az')) {
            fauneRow.classList.remove('hidden-az');
            floreRow.classList.add('hidden-az');
            check = true
        } else if (check == false && floreRow.classList.contains('hidden-az') && !fauneRow.classList.contains('hidden-az')) {
            floreRow.classList.remove('hidden-az');
            fauneRow.classList.add('hidden-az');
            check = true
        }

        console.log("\n--- Iteration end ---")
        changeFauneFlore();
    }, 15000)

}
changeFauneFlore();

// AOS 
AOS.init();

// Secteurs
let nomSecteurDOM = document.querySelector('#nom-secteur');
let secteurs = document.querySelectorAll('.secteur')
let secteurArray = {
    "secteurA": "SAINT-FLOUR 1",
    "secteurB": "MURAT",
    "secteurC": "RIOM-ES-MONTAGNES",
    "secteurD": "YDES",
    "secteurE": "NEUVGLISE",
    "secteurF": "SAINT-FLOUR 2",
    "secteurG": "VIC-SUR-CERE",
    "secteurH": "ARPAJON-SUR-CERE",
    "secteurI": "MAURS",
    "secteurJ": "ST-PAUL DES LANDES",
    "secteurK": "NAUCELLES",
    "secteurL": "MAURIAC",
    "secteurM": "AURILLAC",
}

secteurs.forEach(function(secteur) {
    secteur.addEventListener('mouseenter', function(e) {
        nomSecteurDOM.innerText = secteurArray[secteur.id]
        console.log(secteur.id)
    })
    secteur.addEventListener('click', function(e) {
        nomSecteurDOM.innerText = secteurArray[secteur.id]
        secteurs.forEach(function(secteurZ) { secteurZ.classList.remove('selected'); })
        secteur.classList.add('selected');
    })
})